<template>
  <div class="qr-batch-container">
    <!--  -->
    <div class="main-content">
      <!--  -->
      <div class="list-container">
        <!--  -->
        <div class="date-selector">
          <div class="date-filter">
            <label for="date-filter"></label>
            <input 
              type="date" 
              id="date-filter" 
              v-model="selectedDate"
              @change="handleDateChange"
              :max="today" 
            >
            <div class="quick-dates">
              <div 
                v-for="date in quickDates" 
                :key="date.value"
                class="quick-date"
                :class="{ 'active': date.value === selectedDate }"
                @click="selectQuickDate(date.value)"
              >
                {{ date.label }}
              </div>
            </div>
          </div>
          <div class="date-actions">
            <button @click="resetDateFilter" class="action-btn reset-btn">
              <i class="fas fa-times"></i> 
            </button>
          </div>
          <div class="date-display">
            <span v-if="selectedQuickDate?.type === 'range'">
              {{ formatDate(selectedDate) }}  {{ formatDate(today) }}
            </span>
            <span v-else>
              {{ formattedSelectedDate }}
            </span>
          </div>
        </div>

        <!--  -->
        <div class="list-section">
          <h2><i class="fas fa-print"></i> </h2>
          <div v-if="pendingLuggage.length === 0 && !loading" class="no-data">
            <i class="fas fa-calendar-times"></i> 
            {{ selectedDate ? '' : '' }}
          </div>
          <div v-else>
            <div v-for="dateGroup in groupedPendingLuggage" :key="dateGroup.date">
              <h3 class="date-header">{{ formatGroupDate(dateGroup.date) }}</h3>
              <div class="luggage-grid">
                <div 
                  v-for="luggage in dateGroup.items" 
                  :key="luggage.id" 
                  class="luggage-card" 
                  @click="loadLuggage(luggage.id)"
                  :class="{ 'active': luggageInfo?.id === luggage.id }"
                >
                  <div class="luggage-header">
                    <span class="badge new"></span>
                    <strong>{{ luggage.id }}</strong>
                  </div>
                  <div class="luggage-body">
                    <p><i class="fas fa-user"></i> {{ luggage.guestName }}</p>
                    <p><i class="fas fa-phone"></i> ***-****-{{ getPhoneLast4(luggage.phone) }}</p>
                    <p><i class="fas fa-building"></i> {{ luggage.roomNumber || '' }}</p>
                    <p><i class="fas fa-warehouse"></i> {{ luggage.storageLocation || '' }}</p>
                    <p><i class="fas fa-suitcase"></i> {{ luggage.luggageCount }} </p>
                    <p><i class="fas fa-sticky-note"></i> {{ luggage.remark || '' }}</p>
                    <p><i class="fas fa-clock"></i> {{ formatTime(luggage.checkinTime) }}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <!--  -->
        <div class="list-section">
          <h2><i class="fas fa-redo"></i> </h2>
          <div v-if="printedLuggage.length === 0 && !loading" class="no-data">
            <i class="fas fa-calendar-times"></i> 
            {{ selectedDate ? '' : '' }}
          </div>
          <div v-else>
            <div v-for="dateGroup in groupedPrintedLuggage" :key="dateGroup.date">
              <h3 class="date-header">{{ formatGroupDate(dateGroup.date) }}</h3>
              <div class="luggage-grid">
                <div 
                  v-for="luggage in dateGroup.items" 
                  :key="luggage.id" 
                  class="luggage-card" 
                  @click="loadLuggage(luggage.id)"
                  :class="{ 'active': luggageInfo?.id === luggage.id }"
                >
                  <div class="luggage-header">
                    <span class="badge reprinted"></span>
                    <strong>{{ luggage.id }}</strong>
                  </div>
                  <div class="luggage-body">
                    <p><i class="fas fa-user"></i> {{ luggage.guestName }}</p>
                    <p><i class="fas fa-phone"></i> ***-****-{{ getPhoneLast4(luggage.phone) }}</p>
                    <p><i class="fas fa-building"></i> {{ luggage.roomNumber || '' }}</p>
                    <p><i class="fas fa-warehouse"></i> {{ luggage.storageLocation || '' }}</p>
                    <p><i class="fas fa-suitcase"></i> {{ luggage.luggageCount }} </p>
                    <p><i class="fas fa-sticky-note"></i> {{ luggage.remark || '' }}</p>
                    <p><i class="fas fa-clock"></i> {{ formatTime(luggage.checkinTime) }}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!--  -->
      <div class="preview-sidebar" :class="{ 'has-content': luggageInfo }">
        <div v-if="luggageInfo" class="preview-content">
          <div class="preview-header">
            <h2><i class="fas fa-tags"></i>  - {{ luggageInfo.id }}</h2>
            <div class="guest-info">
              <p><i class="fas fa-user"></i> {{ luggageInfo.guestName }}</p>
              <p><i class="fas fa-phone"></i> ***-****-{{ luggageInfo.phoneLast4 }}</p>
              <p><i class="fas fa-building"></i> {{ luggageInfo.roomNumber || '' }}</p>
              <p><i class="fas fa-warehouse"></i> {{ luggageInfo.storageLocation || '' }}</p>
              <p><i class="fas fa-suitcase"></i> {{ luggageInfo.luggageCount }} </p>
              <p><i class="fas fa-sticky-note"></i> {{ luggageInfo.remark || '' }}</p>
            </div>
          </div>
          
          <div class="qr-grid">
            <button @click="printTickets" class="print-btn">
              <i class="fas fa-print"></i> 
            </button>

            <div class="qr-card">
              <img :src="customerQRCode" alt="">
              <div class="qr-meta">
                <p></p>
              </div>
            </div>
            
            <div class="qr-card">
              <img :src="luggageQRCode" alt="">
              <div class="qr-meta">
                <p></p>
              </div>
            </div>
          </div>
        </div>
        
        <div v-else class="empty-preview">
          <i class="fas fa-info-circle"></i>
          <p></p>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import { ref, computed, onMounted, onUnmounted, watch } from 'vue'
import dayjs from 'dayjs'
import 'dayjs/locale/zh-tw'
import luggageApi from '@/api/luggage'

// dayjs
dayjs.locale('zh-tw')

export default {
  name: 'QrBatch',
  setup() {
    // 
    const customerQRCode = ref('')
    const luggageQRCode = ref('')
    const luggageInfo = ref(null)
    const pendingLuggage = ref([])
    const printedLuggage = ref([])
    const selectedDate = ref('')
    const loading = ref(false)
    const today = ref(dayjs().format('YYYY-MM-DD'))
    const refreshTimer = ref(null) // 
    const isRequesting = ref(false) // 

    const quickDates = ref([
      { label: '', value: today.value, type: 'day' },
      { label: '', value: dayjs().subtract(1, 'day').format('YYYY-MM-DD'), type: 'day' },
      { label: '', value: dayjs().startOf('week').format('YYYY-MM-DD'), type: 'range' },
      { label: '', value: dayjs().startOf('month').format('YYYY-MM-DD'), type: 'range' }
    ])

    // 
    const selectedQuickDate = computed(() => {
      return quickDates.value.find(date => date.value === selectedDate.value)
    })

    // 
    const formattedSelectedDate = computed(() => {
      if (!selectedDate.value) return ''
      
      const date = dayjs(selectedDate.value)
      const today = dayjs()
      
      // 
      if (date.isSame(today, 'day')) {
        return ' (' + date.format('MM/DD') + ')'
      } 
      else if (date.isSame(today.subtract(1, 'day'), 'day')) {
        return ' (' + date.format('MM/DD') + ')'
      }
      else if (date.isSame(today, 'week')) {
        return ' (' + date.format('MM/DD') + ' ~ ' + today.format('MM/DD') + ')'
      }
      else if (date.isSame(today, 'month')) {
        return ' (' + date.format('MM/01') + ' ~ ' + today.format('MM/DD') + ')'
      }
      else {
        return date.format('YYYYMMDD')
      }
    })

    // 
    const groupByDate = (luggageList) => {
      const groups = {}
      
      luggageList.forEach(item => {
        const date = dayjs(item.checkinTime).format('YYYY-MM-DD')
        
        if (!groups[date]) {
          groups[date] = []
        }
        
        groups[date].push(item)
      })
      
      // 
      return Object.keys(groups)
        .map(date => ({
          date,
          items: groups[date].sort((a, b) => 
            new Date(b.checkinTime) - new Date(a.checkinTime)
        )}))
        .sort((a, b) => new Date(b.date) - new Date(a.date))
    }

    // 
    const groupedPendingLuggage = computed(() => {
      return groupByDate(pendingLuggage.value)
    })

    const groupedPrintedLuggage = computed(() => {
      return groupByDate(printedLuggage.value)
    })

    // 
    const formatGroupDate = (dateString) => {
      const today = dayjs().format('YYYY-MM-DD')
      const yesterday = dayjs().subtract(1, 'day').format('YYYY-MM-DD')
      
      if (dateString === today) {
        return ' (' + dayjs(dateString).format('MM/DD') + ')'
      } else if (dateString === yesterday) {
        return ' (' + dayjs(dateString).format('MM/DD') + ')'
      } else {
        return dayjs(dateString).format('YYYYMMDD')
      }
    }

    // 
    const formatTime = (dateTime) => {
      return dayjs(dateTime).format('HH:mm')
    }

    // 
    const formatDate = (dateString) => {
      return dayjs(dateString).format('YYYYMMDD')
    }

    // 
    const getPhoneLast4 = (phone) => {
      // 
      if (!phone || typeof phone !== 'string') {
        return '0000'
      }
      
      // 
      const cleaned = phone.replace(/\D/g, '')
      
      // 4
      if (cleaned.length < 4) {
        return cleaned || '0000'
      }
      
      // 
      return cleaned.slice(-4)
    }

    // 
    const resetDateFilter = () => {
      selectedDate.value = ''
      handleDateChange()
    }

    // 
    const selectQuickDate = (date) => {
      selectedDate.value = date
      handleDateChange()
    }

    // 
    const handleDateChange = () => {
      // 
      fetchLuggageData()
      // 
      restartRefreshTimer()
    }

    // 
    const fetchLuggageData = async () => {
      // 
      if (isRequesting.value) {
        console.log('')
        return
      }
      
      try {
        isRequesting.value = true
        loading.value = true
        
        // 
        const params = {}
        
        if (selectedDate.value) {
          if (selectedQuickDate.value?.type === 'range') {
            //  ()
            params.startDate = selectedDate.value
            params.endDate = today.value
          } else {
            // 
            params.date = selectedDate.value
          }
        }
        
        console.log(':', params)
        
        // 
        const [pendingRes, printedRes] = await Promise.all([
          luggageApi.getPendingForQR(params),
          luggageApi.getPrintedLuggage(params)
        ])
        
        // 
        if (!Array.isArray(pendingRes.data)) {
          console.error(':', pendingRes.data)
          return
        }
        
        if (!Array.isArray(printedRes.data)) {
          console.error(':', printedRes.data)
          return
        }
        
        // Vue
        pendingLuggage.value = [...pendingRes.data]
        printedLuggage.value = [...printedRes.data]
        
        console.log(' - :', pendingLuggage.value.length, ':', printedLuggage.value.length)
        
      } catch (error) {
        console.error(':', error)
      } finally {
        loading.value = false
        isRequesting.value = false
      }
    }

    // 
    const restartRefreshTimer = () => {
      // 
      if (refreshTimer.value) {
        clearInterval(refreshTimer.value)
        console.log('')
      }
      
      // 3
      refreshTimer.value = setInterval(() => {
        console.log('')
        fetchLuggageData()
      }, 3000)
      
      console.log('3')
    }

    // - 
    const loadLuggage = async (luggageId) => {
      try {
        loading.value = true
        
        // 
        const detailRes = await luggageApi.getLuggageById(luggageId)
        const luggageDetail = detailRes.data
        
        // UI remark
        luggageInfo.value = {
          id: luggageDetail.id,
          guestName: luggageDetail.guestName,
          phoneLast4: getPhoneLast4(luggageDetail.phone),
          luggageCount: luggageDetail.luggageCount,
          checkinTime: luggageDetail.checkinTime,
          roomNumber: luggageDetail.roomNumber || '', // 
          storageLocation: luggageDetail.storageLocation || '', // 
          remark: luggageDetail.remark || '' // 
        };
        
        // 
        await generateQRCodes(luggageDetail)
        
      } catch (error) {
        console.error(':', error)
        
        let errorMsg = ''
        if (error.response) {
          if (error.response.status === 404) {
            errorMsg = ''
          } else if (error.response.data && error.response.data.message) {
            errorMsg = error.response.data.message
          }
        }
        
        alert(`${errorMsg}`)
      } finally {
        loading.value = false
      }
    }

    // 
    const generateQRCodes = (luggageDetail) => {
      try {
        // 
        generateFrontendQRCodes(luggageDetail);
      } catch (error) {
        console.error(':', error)
        generateExternalServiceQRCodes(luggageDetail);
      }
    }

    const generateFrontendQRCodes = (luggageDetail) => {
      //  remark 
      const customerContent = JSON.stringify({
        type: "customer_ticket",
        luggageId: luggageDetail.id,
      });
      
      const luggageContent = JSON.stringify({
        type: "luggage_tag",
        luggageId: luggageDetail.id,
      });
      
      const qrcode = require('qrcode-generator');
      
      const customerQr = qrcode(0, 'M');
      customerQr.addData(customerContent);
      customerQr.make();
      customerQRCode.value = customerQr.createDataURL(6);
      
      // 
      const luggageQr = qrcode(0, 'M');
      luggageQr.addData(luggageContent);
      luggageQr.make();
      luggageQRCode.value = luggageQr.createDataURL(6);
    }

    // 
    const generateExternalServiceQRCodes = (luggageDetail) => {
      //  remark
      const customerContent = JSON.stringify({
        type: "customer_ticket",
        luggageId: luggageDetail.id,
        guestName: luggageDetail.guestName,
        remark: luggageDetail.remark || '' // 
      });
      
      //  remark
      const luggageContent = JSON.stringify({
        type: "luggage_tag",
        luggageId: luggageDetail.id,
        guestName: luggageDetail.guestName,
        remark: luggageDetail.remark || '' // 
      });
      
      // 
      customerQRCode.value = `https://api.qrserver.com/v1/create-qr-code/?size=200x200&data=${encodeURIComponent(customerContent)}`;
      luggageQRCode.value = `https://api.qrserver.com/v1/create-qr-code/?size=200x200&data=${encodeURIComponent(luggageContent)}`;
    }

    // 
    const printTickets = async () => {
      try {
        const luggageId = luggageInfo.value.id;
        
        // 
        const alreadyPrinted = printedLuggage.value.some(item => item.id === luggageId);
        
        // 
        if (!alreadyPrinted) {
          try {
            // API
            await luggageApi.markAsQrGenerated(luggageId);
          } catch (apiError) {
            console.error(':', apiError);
          }
          
          // 
          const pendingIndex = pendingLuggage.value.findIndex(item => item.id === luggageId);
          if (pendingIndex !== -1) {
            const [movedItem] = pendingLuggage.value.splice(pendingIndex, 1);
            printedLuggage.value.push(movedItem);
          }
        }
        
        // HTML
        const customerTicket = generateCustomerTicket();
        const luggageTag = generateLuggageTag();
        
        // 
        const printContent = customerTicket + luggageTag;
        
        // 
        const printWindow = window.open('', '_blank');
        printWindow.document.write(`
          <!DOCTYPE html>
          <html>
          <head>
            <title></title>
            <style>
              @page { size: auto; margin: 5mm; }
              body { font-family: Arial, sans-serif; }
              .ticket { 
                page-break-after: always; 
                padding: 10px; 
                border: 1px dashed #ccc; 
                margin-bottom: 10px;
                text-align: center;
                width: 80mm;
              }
              .ticket:last-child { 
                page-break-after: auto; 
              }
              .hotel-logo {
                max-width: 60px;
                margin-bottom: 5px;
              }
              .ticket-header {
                font-weight: bold;
                font-size: 16px;
                margin-bottom: 8px;
              }
              .ticket-info {
                font-size: 20px;
                margin: 4px 0;
                text-align: left;
              }
              .ticket-qr {
                width: 180px;
                height: 180px;
              }
              .terms {
                font-size: 13px;
                text-align: left;
                margin-top: 10px;
              }
              .ticket-info.large-print {
                font-size: 30pt; 
                font-weight: bold;
                margin: 10px 0; 
                word-break: break-all; 
              }
              .field-label {
                color: #222;
                min-width: 90px;
                display: inline-block;
              }
            </style>
          </head>
          <body>
            ${printContent}
          </body>
          </html>
        `);
        printWindow.document.close();
        
        // 
        printWindow.onload = function() {
          printWindow.print();
          // 
          setTimeout(() => {
            printWindow.close();
          }, 100);
        };
        
      } catch (error) {
        console.error(':', error)
        alert('')
      }
    }

    // HTML
    const generateCustomerTicket = () => {
      // 
      const roomNumber = luggageInfo.value.roomNumber || '';
      const storageLocation = luggageInfo.value.storageLocation || '';
      const remark = luggageInfo.value.remark || ''; // 
      
      return `
        <div class="ticket">
          <div class="ticket-header">
            <img src="/images/new_orient_group.jpg" class="hotel-logo"> 
            <div></div>
          </div>
          <div class="divider"></div>
          <div class="ticket-info large-print">
            <span class="field-label">ID:</span> 
            <span>${luggageInfo.value.id}</span>
          </div>
          <div class="ticket-info">
            <span class="field-label">:</span> 
            <span>${luggageInfo.value.guestName}</span>
          </div>
          <div class="ticket-info">
            <span class="field-label">:</span> 
            <span>${luggageInfo.value.phoneLast4}</span>
          </div>
          <div class="ticket-info">
            <span class="field-label">:</span> 
            <span>${roomNumber}</span>
          </div>
          <div class="ticket-info">
            <span class="field-label">:</span> 
            <span>${storageLocation}</span>
          </div>
          <div class="ticket-info">
            <span class="field-label">:</span>   
            <span>${luggageInfo.value.luggageCount} </span>
          </div>
          <div class="ticket-info">
            <span class="field-label">:</span> 
            <span>${remark}</span>
          </div>
          <div class="ticket-info">
            <span class="field-label">:</span> 
            <span>${dayjs(luggageInfo.value.checkinTime).format('YYYY-MM-DD HH:mm:ss')}</span>
          </div>
          <div class="ticket-info">
            <span class="field-label">:</span>
            <span style="border-bottom: 1px solid #333; width: 180px; display: inline-block; margin-left: 10px;"></span>
          </div>
          <img src="${customerQRCode.value}" alt="" class="ticket-qr">
          <div class="terms">
            <div><strong>:</strong></div>
            <div>1.This is our policy to keep Hotel guest's luggage in our storeroom free of charge for no more than three days. We will levy a storage charge of MOP20.00 per day on each item thereafter until they are being claimed.</div>
            <div>2.The Hotel will not be held responsible for any loss or damage of the luggage deposited or articles contained in them.</div>
            <div>3.The Hotel reserves the right to dispose any unclaimed luggage held in our storage for more than FIFTEEN DAYS without any liability.</div>
            <div>4.By using this service, guests acknowledge and agree to these terms.</div>
            <div>1.</div>
            <div>2.</div>
            <div>3.</div>
            <div>4.</div>
          </div>
        </div>
      `;
    }

    // HTML
    const generateLuggageTag = () => {
      // 
      const roomNumber = luggageInfo.value.roomNumber || '';
      const storageLocation = luggageInfo.value.storageLocation || '';
      const remark = luggageInfo.value.remark || ''; // 
      
      return `
      <div class="ticket">
        <div class="ticket-header">
          <img src="/images/new_orient_group.jpg" class="hotel-logo"> 
          <div></div>
        </div>
        <div class="divider"></div>
        <div class="ticket-info large-print">
          <span class="field-label">ID:</span> 
          <span>${luggageInfo.value.id}</span>
        </div>
        <div class="ticket-info">
          <span class="field-label">:</span> 
          <span>${luggageInfo.value.guestName}</span>
        </div>
        <div class="ticket-info">
          <span class="field-label">:</span> 
          <span>${roomNumber}</span>
        </div>
        <div class="ticket-info">
          <span class="field-label">:</span> 
          <span>${storageLocation}</span>
        </div>
        <div class="ticket-info">
          <span class="field-label">:</span> 
          <span>${luggageInfo.value.luggageCount} &nbsp&nbsp;</span>
        </div>
        <div class="ticket-info">
          <span class="field-label">:</span> 
          <span>${remark}</span>
        </div>
        <div class="ticket-info">
          <span class="field-label">:</span> 
          <span>${dayjs(luggageInfo.value.checkinTime).format('YYYY-MM-DD HH:mm:ss')}</span>
        </div>
        <div class="ticket-info">
          <span class="field-label">:</span>
          <span style="border-bottom: 1px solid #333; width: 180px; display: inline-block; margin-left: 10px;"></span>
        </div>
        <img src="${luggageQRCode.value}" alt="" class="ticket-qr">
      </div>
      `;
    };

    // 
    watch(selectedDate, (newDate) => {
      if (newDate && newDate > today.value) {
        selectedDate.value = today.value
        alert('')
      }
    })

    // 
    onMounted(() => {
      // 
      selectedDate.value = today.value
      // 
      fetchLuggageData()
      // 3
      restartRefreshTimer()
    })

    // 
    onUnmounted(() => {
      if (refreshTimer.value) {
        clearInterval(refreshTimer.value)
        console.log('')
      }
    })
    
    return {
      customerQRCode,
      luggageQRCode,
      luggageInfo,
      pendingLuggage,
      printedLuggage,
      selectedDate,
      loading,
      today,
      quickDates,
      selectedQuickDate,
      formattedSelectedDate,
      groupedPendingLuggage,
      groupedPrintedLuggage,
      loadLuggage,
      printTickets,
      resetDateFilter,
      selectQuickDate,
      handleDateChange,
      formatGroupDate,
      formatTime,
      formatDate,
      getPhoneLast4
    }
  }
}
</script>

<style scoped>
/*  */
.luggage-body i.fa-sticky-note {
  color: #f59e0b; /*  */
}

.guest-info i.fa-sticky-note {
  color: #f59e0b;
}

/*  */
:root {
  --primary: #2c5282;
  --primary-light: #4299e1;
  --secondary: #38a169;
  --dark: #0f172a;
  --light: #f8fafc;
  --gray: #94a3b8;
  --light-gray: #334155;
  --border-radius: 12px;
  --transition: all 0.3s ease;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Segoe UI', 'Microsoft JhengHei', sans-serif;
  background: linear-gradient(135deg, #1e3a8a 0%, #0f172a 100%);
  color: #e2e8f0;
  line-height: 1.6;
  min-height: 100vh;
}

.qr-batch-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
  padding: 20px;
}

.main-content {
  display: flex;
  flex: 1;
  overflow: hidden;
  gap: 20px;
  max-height: calc(100vh - 120px);
}

.list-container {
  flex: 1;
  overflow-y: auto;
  padding-right: 10px;
  display: flex;
  flex-direction: column;
  border-radius: var(--border-radius);
  padding: 20px;
  background: rgba(15, 23, 42, 0.7);
  border: 1px solid rgba(99, 102, 241, 0.2);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
}

.preview-sidebar {
  width: 380px;
  border-radius: var(--border-radius);
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  position: relative;
  transition: var(--transition);
  background: rgba(15, 23, 42, 0.7);
  border: 1px solid rgba(99, 102, 241, 0.2);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
}

.preview-sidebar.has-content {
  border-left: 4px solid #6366f1;
}

.empty-preview {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: var(--gray);
  text-align: center;
  padding: 40px;
}

.empty-preview i {
  font-size: 4rem;
  margin-bottom: 25px;
  color: #334155;
  opacity: 0.7;
}

.empty-preview p {
  font-size: 1.4rem;
  color: var(--gray);
  font-weight: 500;
  max-width: 300px;
  line-height: 1.8;
}

.date-selector {
  background: rgba(30, 41, 59, 0.6);
  border-radius: var(--border-radius);
  padding: 18px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  gap: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  border: 1px solid var(--light-gray);
}

.date-filter {
  display: flex;
  align-items: center;
  gap: 12px;
  flex-wrap: wrap;
}

.date-filter label {
  font-weight: 600;
  color: #e2e8f0;
  font-size: 1.05rem;
  min-width: 100px;
}

.date-filter input[type="date"] {
  padding: 10px 14px;
  border: 1px solid var(--light-gray);
  border-radius: 8px;
  font-size: 1rem;
  min-width: 200px;
  background: rgba(15, 23, 42, 0.8);
  color: #e2e8f0;
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.3);
}

.date-actions {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
}

.action-btn {
  padding: 10px 18px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 0.95rem;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: var(--transition);
  border: none;
  font-weight: 500;
}

.reset-btn {
  background: #64748b;
  color: white;
}

.reset-btn:hover {
  background: #475569;
  transform: translateY(-2px);
  box-shadow: 0 2px 8px rgba(100, 116, 139, 0.4);
}

.date-display {
  font-weight: 600;
  color: #cbd5e1;
  font-size: 1.1rem;
  padding: 8px 0;
  border-top: 1px dashed var(--light-gray);
  margin-top: 10px;
}

.list-section {
  background: rgba(30, 41, 59, 0.6);
  border-radius: var(--border-radius);
  padding: 20px;
  margin-bottom: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  flex: 1;
  display: flex;
  flex-direction: column;
  border: 1px solid var(--light-gray);
}

.list-section h2 {
  margin-top: 0;
  padding-bottom: 16px;
  border-bottom: 2px solid var(--light-gray);
  color: #e2e8f0;
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 1.4rem;
}

.list-section h2 i {
  color: #818cf8;
}

.no-data {
  text-align: center;
  padding: 40px 20px;
  background: rgba(15, 23, 42, 0.5);
  border-radius: var(--border-radius);
  color: var(--gray);
  font-size: 1.15rem;
  border: 2px dashed var(--light-gray);
  margin: 20px 0;
  font-weight: 500;
}

.no-data i {
  margin-right: 10px;
  font-size: 1.8rem;
  color: #475569;
}

.luggage-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 18px;
  margin-top: 18px;
  flex: 1;
  overflow-y: auto;
  padding-bottom: 10px;
}

.luggage-card {
  border: 1px solid var(--light-gray);
  border-radius: var(--border-radius);
  padding: 18px;
  background: rgba(15, 23, 42, 0.7);
  cursor: pointer;
  transition: var(--transition);
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.luggage-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
  border-color: #6366f1;
  background: rgba(30, 41, 59, 0.8);
}

.luggage-card.active {
  border: 2px solid #6366f1;
  background: rgba(30, 41, 59, 0.9);
}

.luggage-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
  padding-bottom: 12px;
  border-bottom: 1px dashed var(--light-gray);
}

.badge {
  padding: 5px 12px;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
}

.badge.new {
  background: #10b981;
  color: white;
}

.badge.reprinted {
  background: #f59e0b;
  color: #1e293b;
}

.luggage-header strong {
  font-size: 1.1rem;
  color: #e2e8f0;
}

.luggage-body p {
  margin: 10px 0;
  font-size: 0.95rem;
  display: flex;
  align-items: center;
  gap: 10px;
  color: #cbd5e1;
}

.luggage-body i {
  width: 20px;
  color: #818cf8;
  text-align: center;
}

.date-header {
  margin: 22px 0 16px;
  padding-bottom: 10px;
  border-bottom: 2px solid var(--light-gray);
  color: #e2e8f0;
  font-size: 1.25rem;
  position: relative;
  padding-left: 18px;
  font-weight: 600;
}

.date-header::before {
  content: "";
  position: absolute;
  left: 0;
  top: 5px;
  bottom: 5px;
  width: 5px;
  background: #6366f1;
  border-radius: 3px;
}

.preview-content {
  padding: 22px;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.preview-header {
  margin-bottom: 22px;
  padding-bottom: 18px;
  border-bottom: 2px solid var(--light-gray);
}

.preview-header h2 {
  font-size: 1.35rem;
  color: #e2e8f0;
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 15px;
}

.preview-header h2 i {
  color: #818cf8;
}

.guest-info {
  display: flex;
  flex-direction: column;
  gap: 8px;
  font-size: 1.05rem;
  background: rgba(30, 41, 59, 0.6);
  padding: 12px 18px;
  border-radius: 8px;
  margin-top: 10px;
  border: 1px solid var(--light-gray);
}

.guest-info p {
  display: flex;
  align-items: center;
  gap: 10px;
  margin: 0;
  color: #cbd5e1;
  font-weight: 500;
}

.qr-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
  gap: 18px;
  overflow-y: auto;
  padding-bottom: 20px;
  flex: 1;
}

.qr-card {
  border: 1px solid var(--light-gray);
  padding: 18px;
  text-align: center;
  border-radius: var(--border-radius);
  transition: var(--transition);
  background: rgba(15, 23, 42, 0.7);
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.qr-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
  border-color: #6366f1;
  background: rgba(30, 41, 59, 0.8);
}

.qr-card img {
  width: 140px;
  height: 140px;
  object-fit: contain;
  margin-bottom: 12px;
  border: 1px solid var(--light-gray);
  padding: 5px;
  background: white;
  border-radius: 8px;
}

.qr-meta {
  margin: 12px 0;
  font-size: 1.rem;
  font-weight: 500;
  color: #e2e8f0;
}

.print-btn {
  background: #4f46e5;
  color: white;
  border: none;
  padding: 10px 18px;
  border-radius: 8px;
  cursor: pointer;
  margin-top: 12px;
  width: 100%;
  transition: var(--transition);
  font-weight: 500;
  font-size: 0.95rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}


.print-btn:hover {
  background: #6366f1;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(99, 102, 241, 0.4);
}

.quick-dates {
  display: flex;
  gap: 10px;
  margin-top: 15px;
  flex-wrap: wrap;
}

.quick-date {
  padding: 8px 16px;
  background: rgba(30, 41, 59, 0.7);
  border: 1px solid var(--light-gray);
  border-radius: 20px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: var(--transition);
  font-weight: 500;
  color: #cbd5e1;
}

.quick-date:hover, .quick-date.active {
  background: #4f46e5;
  color: white;
  border-color: #6366f1;
}

@media (max-width: 1200px) {
  .qr-grid {
    grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
  }
}

@media (max-width: 992px) {
  .main-content {
    flex-direction: column;
    padding: 0;
  }
  
  .preview-sidebar {
    width: 100%;
    max-height: 50vh;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 100;
    box-shadow: 0 -5px 15px rgba(0, 0, 0, 0.3);
    border-radius: 16px 16px 0 0;
  }
  
  .list-container {
    padding-right: 0;
    padding-bottom: 15px;
  }
  
  .luggage-grid {
    grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  }
}

@media (max-width: 768px) {
  .date-selector {
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
  }
  
  .date-filter {
    width: 100%;
  }
  
  .date-filter input[type="date"] {
    flex: 1;
    min-width: auto;
  }
  
  .reset-btn {
    width: 100%;
    justify-content: center;
  }
  
  .luggage-grid {
    grid-template-columns: 1fr;
  }
  
  .qr-grid {
    grid-template-columns: repeat(auto-fill, minmax(130px, 1fr));
  }
}

@media (max-width: 480px) {
  .header {
    padding: 15px;
  }
  
  .header h1 {
    font-size: 1.3rem;
  }
  
  .list-section {
    padding: 15px;
  }
  
  .preview-header h2 {
    font-size: 1.15rem;
  }
  
  .guest-info {
    flex-direction: column;
    gap: 8px;
    font-size: 0.95rem;
  }
  
  .qr-card img {
    width: 120px;
    height: 120px;
  }
}
</style>